import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniSteps from '@/components/uni-steps/uni-steps.vue';
import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import json from '@/json';

export default {
	components: {
		uniNavBar,
		uniSteps,
		uniList,
		uniListItem
	},
	data() {
		return {
			id: '',
			showtip1: 'center',
			detailData: {},
			imgList: [],
			active: 1,
			latitude: 30.663429,
			longitude: 104.072422,
			covers: [
				{
					latitude: 30.663429,
					longitude: 104.072422,
					iconPath: '../../static/location.png',
					width: 50,
					height: 50
				}
			]
		};
	},
	onLoad(options) {
		var datas = JSON.parse(options.data);
		this.id = datas.id;
		console.log(this.id);
		this.loadNewsList();
	},
	methods: {
		//获取推荐列表
		async loadNewsList() {
			this.detailData = json.detail.data;
			console.log(this.detailData);
		},
		statusChange(index) {
			switch (index) {
				case 1:
					return '待处理';
					break;
				case 2:
					return '处理中';
					break;

				case 3:
					return '已处理';
					break;
				default:
					return '待处理';
			}
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		scroll(e) {
			console.log(e.detail.scrollTop);
		},
		//图片预览
		previewImage(e) {
			var current = e.target.dataset.src;
			console.log('current');
			console.log(current);
			uni.previewImage({
				current: current,
				urls: this.detailData.imgList
			});
		}
	}
};
